import React, { Component } from 'react';
import axios from 'axios';

export default class Home extends Component {
  state = {
    posts: []
  };
  async componentDidMount() {
    try {
      const getAllPosts = await axios.get('/api/get/allposts');
      if (getAllPosts) {
        console.log(getAllPosts.data);
        this.setState({ posts: getAllPosts.data });
      }
    } catch (error) {
      console.log(error);
    }

    // .then(res => console.log(res.data))
    // .then(res => this.props.set_posts(res.data))
    // .then(() => this.add_posts_to_state(this.props.posts))
    // .catch(err => console.log(err));
    // axios
    //   .get('/api/get/allposts')
    //   .then(res => console.log(res.data))
    //   .then(res => this.props.set_posts(res.data))
    //   .then(() => this.add_posts_to_state(this.props.posts))
    //   .catch(err => console.log(err));
  }
  render() {
    if (this.state.posts === []) {
      console.log(this.state.posts);
      return <div>Loading ...</div>;
    } else {
      console.log(this.state.posts);
      const displayPosts = this.state.posts.map(post => {
        console.log(post);
        return (
          <tr>
            <td>{post.pid}</td>
            <td>{post.author}</td>
            <td>{post.title}</td>
            <td>{post.body}</td>
          </tr>
        );
      });
      return (
        <div>
          <div>Home</div>
          <table className="table">
            <tr>
              <th>ID</th>
              <th>Author</th>
              <th>Title</th>
              <th>Body</th>
            </tr>
            {displayPosts}
          </table>
        </div>
      );
    }
  }
}
